body {
  background-color: #f6f6f6;
}

.tool-box {
  font-size: 12px;
  color: white;
  text-align: center;
  align-items: center;
  letter-spacing: 0.3ch;
  text-transform: uppercase;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  padding-bottom: 7.5px;
  padding-right: 7.5px;
  flex-direction: column;
  width: 55px;
  background-color: #444444;
  margin: 0.5vh;
  border-radius: 5px;
}

.center-text {
  width: 55px;
  text-align: center;
  padding-left: 8.5px;
}

.tool-box-2 {
  text-align: center;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  right: 0;
  width: 165px;
  background-color: #444444;
  margin: 0.5vh;
  border-radius: 5px;
  transition: 0.75s;
  color: white;
  padding-left: 7.5px;
  letter-spacing: 0.3ch;
  text-transform: uppercase;
}

.tool-box-3 {
  background-color: #444444;
  align-items: center;
  display: flex;
  flex-direction: row;
  position: absolute;
  bottom: 0;
  left: 0;
  margin-bottom: 5px;

  margin-left: 5px;
  padding-bottom: 7.5px;
  width: 102.5px;
  border-radius: 5px;
}

.tool-box-4 {
  background-color: #444444;
  align-items: center;
  display: flex;
  flex-direction: column;
  color: white;
  letter-spacing: 0.3ch;
  text-transform: uppercase;
  width: 165px;
  position: absolute;
  bottom: 0;
  right: 0;
  margin-bottom: 5px;
  margin-left: 5px;
  margin-right: 5px;
  padding-bottom: 5px;
  border-radius: 5px;
  transition: 0.25s ease-in-out;
}
.tool-box-4:hover {
  width: 35%;
  font-size: 64px;
  transition: 0.25s ease-in-out;
  border-radius: 25px;
}
.canva {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #cccccc;
}

.tool {
  padding-top: 6.5px;
  padding-left: 6.5px;
  width: 80%;
  height: 80%;
}

.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.tool-sq {
  display: flex;
  width: 40px;
  height: 40px;
  background-color: #666666;
  border-radius: 5px;
  margin-left: 7.5px;
  margin-top: 7.5px;
}
.tool-sq:hover {
  display: flex;
  width: 40px;
  height: 40px;
  background-color: #888888;
  cursor: pointer;
}

img {
  margin-left: 1px;
}

.tool-sq:focus {
  display: flex;
  width: 40px;
  height: 40px;
  background-color: #333333;
  cursor: pointer;
}

[type="radio"] {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

.width-div {
  display: flex;
  flex-direction: row;
  padding-bottom: 10px;
  flex-wrap: wrap;
}
